/* Login Page Base */
.login-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: linear-gradient(to right, #b5b8c9, #efeef1);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.login-container {
  background: #ffffff;
  padding: 40px 30px;
  border-radius: 12px;
  box-shadow: 0px 12px 28px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 420px;
}

/* Headings */
.login-title {
  text-align: center;
  margin-bottom: 25px;
  color: #333;
  font-size: 24px;
}

/* Form */
.login-form {
  display: flex;
  flex-direction: column;
}

.input-container {
  display: flex;
  align-items: center;
  background-color: #f1f3f4;
  border: 1px solid #ccc;
  border-radius: 6px;
  padding: 10px;
  margin-bottom: 20px;
  position: relative;
}

.input-icon {
  margin-right: 10px;
  color: #555;
}

.input-field {
  flex: 1;
  border: none;
  outline: none;
  background: transparent;
  font-size: 15px;
}

.password-toggle {
  position: absolute;
  right: 10px;
  cursor: pointer;
  color: #666;
}

/* Buttons */
.signin-button {
  background-color: #3f51b5;
  color: #fff;
  border: none;
  padding: 12px;
  font-size: 16px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.signin-button:hover {
  background-color: #2e3ca9;
}

.forgot-password {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 15px;
}

.forgot-password-button {
  background: none;
  border: none;
  color: #3f51b5;
  cursor: pointer;
  font-size: 14px;
  text-decoration: underline;
}

/* Modal */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  padding: 30px;
  border-radius: 10px;
  width: 90%;
  max-width: 400px;
}

.modal-title {
  margin-bottom: 15px;
  color: #333;
  font-size: 20px;
}

.modal-form {
  display: flex;
  flex-direction: column;
}

.modal-message {
  font-size: 14px;
  margin-top: 10px;
}

.modal-message.success {
  color: green;
}

.modal-message.error {
  color: red;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.modal-button {
  padding: 10px 16px;
  border: none;
  border-radius: 6px;
  font-size: 14px;
  cursor: pointer;
}

.modal-button.cancel {
  background-color: #ccc;
}

.modal-button.submit {
  background-color: #3f51b5;
  color: white;
}

p {
  text-align: center;
  margin-top: 20px;
}

p a {
  color: #3f51b5;
  text-decoration: none;
}
